<script>
import Vue from "vue";
import VueRouter from 'vue-router'
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';
import { print } from "util";

Vue.use(Vuetify)

export default Vue.extend({
  name: 'app',
  data(){
    return {
      doors: '',
      doorReal: 0,
      calls: '',
      callReal: 0,
      letters: '',
      letterReal: 0,
      texts: '',
      textsReal: 0,
      people: '',
      peopleReal: 0
    }
  },
  computed:{
    goToResults() {
      return this.$route.params.results
    },
    total: function () {
      // `this` points to the vm instance

      if(this.doors === ''){
        this.doors = 0
      }
      this.doorReal = this.doors/14

      if(this.calls === ''){
        this.calls = 0
      }
      this.callReal = this.calls/20

      if(this.letters === ''){
        this.letters = 0
      }
      this.letterReal = this.letters/33

      if(this.texts === ''){
        this.texts = 0
      }
      this.textsReal = this.texts/500

      if(this.people === ''){
        this.people = 0
      }
      this.peopleReal = this.people

      return Math.ceil(this.doorReal + this.callReal + this.letterReal + this.textsReal + this.peopleReal);
    }
  },
  methods: {
    gotBack() { 
      windows.history.length > 1
         ? this.$router.go(-1)
         : this.$router.push('/')
    },
    getTotal(doors,calls,letters,texts,people) {
      door = parseInt(doors);
      if(door === NaN){
        door = 0
      }
      call = parseInt(calls)
      if(call === NaN){
        call = 0
      }
      letter = parseInt(letters)
      if(letter === NaN){
        letter = 0
      }
      text = parseInt(text)
      if(text === NaN){
        text = 0
      }

      people = parseInt(people)
      if(people === NaN){
        people = 0
      }
      print(door+call+letter+text+people);
    }
  }

});

</script>

